.grid {
	position: relative;
	margin: 0 auto;
	list-style: none;
	text-align: center;
     @include grid;
    }

/* Common style */
.grid figure {
	position: relative;
	float: left;
	overflow: hidden;
	width: 100%;
    height: 100%;
	background: white;
	text-align: center;
	cursor: pointer;
    margin: 0;
}



.grid figure figcaption {
	padding: 0;
	color: black;
	text-transform: uppercase;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}

.grid figure figcaption::before,
.grid figure figcaption::after {
	pointer-events: none;
}

.grid figure figcaption,
.grid figure figcaption > a {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

/* Anchor will cover the whole item by default */
/* For some effects it will show as a button */
.grid figure figcaption > a {
	z-index: 100;
    white-space: nowrap;
	font-size: 0;
	opacity: 0;
}

.grid figure h2 {
	font-weight: 300;
    
    border-bottom: 1px solid black;
    display: inline-block;
}

.grid figure h2 span {
	font-weight: 800;
}

.grid figure h2,
.grid figure p {
	margin: 0;
    font-family: 'Tinos', serif;
}

.grid figure p {
	letter-spacing: 1px;
	font-size: 68.5%;
    
}


