.background-div {
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
   @include moving-background-image;
}


/* Individual effects */

/*---------------*/
/***** PROJECT *****/
/*---------------*/

figure.effect-project .background-div {
	max-width: none;
	width: -webkit-calc(100% + 50px);
	width: calc(100% + 50px);
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
	-webkit-transform: translate3d(-40px,0, 0);
	transform: translate3d(-40px,0,0);
   
}

figure.effect-project figcaption {
	text-align: left;
}

figure.effect-project figcaption > div {
	position: absolute;
	bottom: 0;
	left: 0;
	padding: 0 0 0 20px;
	width: 100%;
	height: 50%;
}

figure.effect-project h2,
figure.effect-project p {
	-webkit-transform: translate3d(0,40px,0);
	transform: translate3d(0,40px,0);
}

figure.effect-project h2 {
	-webkit-transition: -webkit-transform 0.35s;
	transition: transform 0.35s;
}

figure.effect-project p {
	color: rgba(0,0,0,0.8);
	opacity: 0;
	-webkit-transition: opacity 0.2s, -webkit-transform 0.35s;
	transition: opacity 0.2s, transform 0.35s;
}

figure.effect-project:hover .background-div,
 {
    background-color:$gray-color;
	opacity: .4;
}

figure.effect-project:hover p {
    opacity:.9;
}

figure.effect-project:hover .background-div,
figure.effect-project:hover h2,
figure.effect-lily:hover p {
	-webkit-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
    
}



figure.effect-project:hover p {
	-webkit-transition-delay: 0.05s;
	transition-delay: 0.05s;
	-webkit-transition-duration: 0.35s;
	transition-duration: 0.35s;
}


